<template>
<div class="pagination">
      <ul class="pagination-list-item">
            <li class="paginationList" :class="{'active': pageNumber === page}" v-for="pageNumber in pages" :key="pageNumber" @click="paginateBtn(pageNumber)">{{ pageNumber }}</li>
          
      </ul>
</div>
</template>

<script lang="ts">
import {
      defineComponent,

} from 'vue';

export default defineComponent({
      data() {
            return {

            }
      },
      model: {
            prop: 'page',
            event: 'paginate'
      },
      props: [
            'page',
            'count',
            'perPage'
      ],
      computed: {
            pages(): number {
                  return Math.ceil(this.count / this.perPage)
            }
      },
      methods: {
            paginateBtn(page: number) {
                  this.$emit('update:page', page)
            }
      }

});
</script>
